<template>
  <div class="bill">
      <div class="bill-top">
          <div class="bill-top-logo">
             <van-notice-bar
                text="爱琦记账本--------------v0.0.1-------------测试版本"
                left-icon="volume-o"
                />
          </div>
          <div class="bill-top-details">
            <div class="bill-top-details-date" @click="show = true">
                {{selectYear}} <br><font>{{selectMonth}}</font>
            </div>
            <div class="bill-top-details-export">
                收入<br><font>0.00</font>
            </div>
            <div class="bill-top-details-import">
                支出<br><font>777.00</font>
            </div>
          </div>    
      </div>
      <div class="bill-detail">
          <div class="bill-detail-top">
          </div>
          <div class="bill-detail-body">
            <demo-section name="pull-refresh">
                <van-pull-refresh
                v-model="isLoading"
                @refresh="onRefresh"
                >
                <van-list
                        v-model="list[0].loading"
                        :finished="list[0].finished"
                        :finished-text="finishedText"
                        @load="onLoad(0)"
                    >
                        <van-cell
                        v-for="item in list[0].items"
                        :key="item"
                        >
                        {{item}}
                        </van-cell>
                    </van-list>
                </van-pull-refresh>
            </demo-section>
          </div>
           <div class="bill-detail-top">
          </div>
      </div>
      <div class="bill-Record" @click="showBill = true">
          <img src="../assets/write.png">
      </div >
      <div>
        <van-popup
            v-model="show"
            position="bottom"
            >
            <van-datetime-picker
                v-model="currentDate"
                type="year-month"
                @confirm="selYearMonth"
                @cancel="show = false"
            />
        </van-popup>
         <van-popup
            v-model="showBill"
            position="bottom"
            >
            <div class="selbill">
                <bill @closeBill="toCloseBill" @openDateTime="OverLayClick"></bill>
            </div>
        </van-popup>
        <div  v-if="OverLay" class="van-overlay" style="z-index: 2000;"></div>
    </div>
  </div>
</template>
<script>
import {List,Toast ,Tab, Tabs, PullRefresh,Cell,NoticeBar,Popup,DatetimePicker} from 'vant';
import bill from './bill.vue';
export default {
  name: 'alibabaPay',
  components: {
      [List.name]: List,
      [Toast.name]: Toast,
      [Tab.name]: Tab,
      [Tabs.name]: Tabs,
      [PullRefresh.name]: PullRefresh,
      [Cell.name]: Cell,
      [NoticeBar.name]: NoticeBar,
      [Popup.name]: Popup,
      [DatetimePicker.name]: DatetimePicker ,
      bill
  },
  data() {
    return {
      list: [{
        items: [],
        refreshing: false,
        loading: false,
        error: false,
        finished: false
      }],
      selectYear: "",
      selectMonth: "",
      count: 0,
      isLoading: false,
      basicUsage:"123",
      text: "刷新次数",
      success: "刷新成功",
      finishedText: "没有更多了",
      show: false,
      showBill: false,
      currentDate: new Date(),
      OverLay: false
    };
  },
  watch(){
      
  },
  created(){
      this.init();
  },
  methods: {
    init() {
        this.selectYear = "2019年"
        this.selectMonth = "01月"
    },
    selYearMonth(){
        this.selectYear = this.currentDate.getFullYear() + "年"
        let selectMonth = parseInt(this.currentDate.getMonth())  + 1
        selectMonth = selectMonth<10 ? "0"+selectMonth : selectMonth 
        this.selectMonth =  selectMonth + "月"
        this.show = false;
    },
    onLoad(index) {
      const list = this.list[index];
      setTimeout(() => {
        for (let i = 0; i < 10; i++) {
          const text = list.items.length + 1;
          list.items.push(text < 10 ? '0' + text : text);
        }
        list.loading = false;
        // show error info in second demo
        if (index === 1 && list.items.length === 10 && !list.error) {
          list.error = true;
        } else {
          list.error = false;
        }
        if (list.items.length >= 40) {
          list.finished = true;
        }
      }, 500);
    },
    onRefresh(index) {
      const list = this.list[index];
      setTimeout(() => {
        list.items = [];
        list.error = false;
        list.finished = false;
        list.refreshing = false;
        window.scrollTo(0, 10);
      }, 1000);
    },
     onRefresh() {
      setTimeout(() => {
        this.$toast(this.success);
        this.isLoading = false;
        this.count++;
      }, 500);
    },
    toCloseBill(){
        this.showBill = false
    },
    OverLayClick(value){
        this.OverLay = value
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
html,body{
	height: 100%;
	width: 100%;
}
#app{
    color: rgb(30, 41, 61);
    height: 100%;
    overflow: hidden;
     background: #BEBECA;
}
.bill{
    display: flex;
    flex-direction: column; 
    height: 100%;
    &-top{
        height: 93px;
        background: #FF69B4;
        &-details{
            //background: black;
            display: flex;
            flex-direction: row;
            font{
                font-weight: bold;
                font-size: 23px;
            }
            &-date{
                padding-left: 5%;
                padding-top: 2px;
                padding-bottom: 2px;
                width: 20%;
                //background: brown;
            }
            &-import{
                padding-left: 5%;
                padding-top: 2px;
                padding-bottom: 2px;
                width: 40%;
                //background:cadetblue;
            }
            &-export{
                padding-left: 5%;
                padding-top: 2px;
                padding-bottom: 2px;
                width: 40%;
                //background:cornflowerblue;
            }
        }
    }
    &-detail{
    display: flex;
    flex-direction: row;
    flex-grow:1;
        &-top{
            height: 20px;
             flex-grow:1;
            background: #FF69B4;
        }
        &-body{

            flex-grow:20;
            background: white;
            border-top: yellow 2px solid;
        }
    }
    &-Record{
         height:60px;
         background: #FF69B4;
         text-align: center;
         line-height: -10%;
         padding: 2px;
    }
    &-DateSelect{
        height:60px;
        background: 100px;
    }
}
  .van-pull-refresh {
      overflow:scroll;
      height: 100%;
  }
  .selbill{
      height: 442px;
      background: white;
  }

</style>
